<template>
    <div>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-top:15px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="编号"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getUsers">查询</el-button>
                </el-form-item>
            </el-form>
        </el-col>


        <el-table :data="userInfoList" style="width: 100%" >
            <el-table-column type="index" width="80">
            </el-table-column>
            <el-table-column prop="bicycleNo" label="单车编号" width="250">
            </el-table-column>
            <el-table-column prop="rentTime" label="租车时间" width="250">
            </el-table-column>
            <el-table-column prop="createTime" label="创建时间" width="250">
            </el-table-column>
            <el-table-column prop="updateTime" label="修改时间" width="250">
            </el-table-column>
            <!--第二步  开始进行修改和查询操作-->
            <!-- <el-table-column label="操作" align="center" min-width="100">

                <template slot-scope="scope">
                    <el-button type="primary" @click="modifyUser(scope.row)">租用</el-button>
                </template>
            </el-table-column> -->
        </el-table>
    </div>

</template>

<script>
    import axios from 'axios';
    import qs from 'qs';
    export default {
        name: 'home',
        inject:['reload'],
        data() {
            return {
                userInfoList: [],
                
                filters: {
                    name: ''
                },
                token:'',
                user:{}
            }
        },

        methods: {
            
            loadData() {
                // let param = {filter:this.filters.name};
                this.axios.get('record/usageRecord?id='+this.user.id).then((res) => {
                    console.log(res);                    
                    this.userInfoList = res.data.data;
                });


            },
            getUsers() {
                this.axios.get("record/findByNoLikeNoRent",{
                    params:{
                        bicycleNo:this.filters.name,
                        userId:this.user.id
                    }
                }).then(res=>{
                    console.log("res:",res);
                    this.userInfoList = res.data.data;
                })
            },
            modifyUser(row) {
                console.log("id: ",row.id);
                this.axios.post("bicycle/rent",this.$qs.stringify({id:row.id,userId:this.user.id})).then(res=>{
                    console.log("res: ",res);
                    if(res.data.code===200){
                        this.$message({
                            message:"租用成功",
                            type:'success'
                        })
                        this.reload();
                    }else{
                            this.$message({
                                message: res.data.message,
                                type:'error'
                            });
                            
                        this.reload();
                    }
                })
            },
        
        },
        created(){
            this.user = this.$store.state.user.accessToken;
            console.log("user: ",this.user);
            
            this.loadData();
        }
    }
</script>

<style>
body {
        background: #DFE9FB;
    }
.demo-ruleForm{
  margin-left: 10%;  
  width: 80%;
}
.el-table .success-row {
    background: #f0f9eb;
  }
</style>


